<script>
  import {Select, Option, Button, Input, DatePicker, Radio, Message, MessageBox} from 'element-ui'
  import Editor from '@tinymce/tinymce-vue'
  import MySelect from 'src/components/UIComponents/Select'
  import MyMoney from 'src/components/UIComponents/Money'
  import { mapState } from 'vuex'

  export default {
    components: {
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElInput: Input,
      ElDatePicker: DatePicker,
      ElRadio: Radio,
      editor: Editor,
      MySelect,
      MyMoney
    },
    created() {
      let self = this;
      this.$validator.extend('earlier', {
        getMessage(field, val) {
          return 'Thi gian kt thc phi ln hn thi gian bt u'
        },
        validate(value, field) {
          let startParts = self.form.start_date
          let momentStart = new Date(startParts).getTime();
          let momentEnd = new Date(value).getTime();
          return momentEnd > momentStart;
        }
      })
    },
    data () {
      return {
        status: {
          options: [
            {
              title: "Hin th",
              value: "active"
            }, {
              title: "n",
              value: "inactive"
            }, {
              title: "Ht hn",
              value: "expried"
            }
          ]
        },
        types: {
          options: [
            {
              title: "Gim theo n gi",
              value: "value"
            }, {
              title: "Gim theo phn trm",
              value: "percent"
            }, {
              title: "Min ph giao hng",
              value: "freeship"
            }
          ]
        }
      }
    },

    computed: {
      form: {
        get () {
          return this.$store.state.couponDetail;
        },
        set (value) {}
      }
    },

    mounted() {
      let id = this.$route.params.id;
      this.$store.dispatch('fetchCouponDetail', id);

      this.$store.dispatch('setPageTitle', 'Cp nht m gim gi');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Xa',
        type: 'warning',
        icon: '',
        callback: this.remove
      }, {
        label: 'Cp nht',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },

    methods: {
      cancel() {
        this.$router.push({name: 'AllCoupons'});
      },
      remove(){
        let self = this;
        MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
          confirmButtonText: 'ng ',
          cancelButtonText: 'Hy b',
          type: 'warning',
          center: true
        }).then(() => {
          self.$store.dispatch('removeCoupon', self.form.id).then(res => {
            Message({
              type: 'success',
              message: ' xa m gim gi'
            });
            self.$router.push({name: 'AllCoupons'});
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hy b'
          });
        })
      },
      save() {
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('updateCoupon', self.form).then(res => {
              Message({
                message: 'Cp nht thnh cng',
                type: 'success'
              });
            }, res => {
              Message({
                message: res.message,
                type: 'danger'
              });
            });
          }
        })
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
